import React,{ useState } from 'react'
import { Card, Form, Modal, Row, Col, Select, Button } from 'antd';
import Flex from 'components/shared-components/Flex'
import { Typography } from "antd";
import List from './list';
import moment from 'moment';
import { DatePicker } from 'antd';
import { CloudDownloadOutlined } from '@ant-design/icons';

const { RangePicker } = DatePicker;
const { Title } = Typography;
const { Option } = Select;
let stockTotal = 0;
let openingStock = '';
let reportData = [];
const Report = ({systemData}) => {
	const [form] = Form.useForm();
	const [ fdate, setFdate ] = useState(0)
	const [ product, setProduct ] = useState('')
	const [ store, setStore ] = useState('')
	const [visible, setVisible] = useState(false);
	const [loading, setLoading] = useState(false);
	const [loaded, setLoaded] = useState(false);
	let { products, productsLoading, stores, storesLoading, singleShop, multiShop, records, recordsLoading, shortTypes, userData } = systemData;
	records = recordsLoading ? [] : records.docs.filter(x => x.data().refunded === false && x.data().source === store && x.data().purchase === false && x.data().productName === product);
	const rangeConfig = {
		rules: [{ type: 'array', required: true, message: 'Please select time!' }],
	  };
	 
	let allItems = [];
	let allProducts = [];
	if(!productsLoading){
		let allItem = [];
		let allProduct = [];
		products.docs.filter(x => x.data()[store] === true).map((doc, index) => {    
			allProduct = {
				...allProduct,
				[doc.data()._id] : {...doc.data()},
			  }
			allItem = [
				...allItem,
				{label: doc.data().productName, value: doc.id, index:index},
			]
			return true;
		})
		allItems = allItem;
		allProducts = allProduct;
		if(loading){
			setLoading(false);
		}
	}  

	if(!recordsLoading && product && store){
		let body = [...records]; 
		body = body.filter(x => x.data().date < fdate && x.data().productName === product);
		let {pkgUnit,qtyPkg,ctnSqm,productType} = allProducts[product];
		let initialStock = 0;
		if(allProducts[product].usedStore !== undefined){
			allProducts[product].usedStore.split('**;;@!*.').map(stored =>{
				if(stored !== store) {return true}
				return initialStock += allProducts[product][`${stored}pcsStock`] + (allProducts[product][`${stored}pkgStock`]*allProducts[product].qtyPkgBk);
			})
		}

		stockTotal = body.reduce((accumulator, currentElement) => {
			let quantity = currentElement.data().purchasedType === '0' ? currentElement.data().quantity : (currentElement.data().purchasedType === '1' ? currentElement.data().quantity * currentElement.data().qtyPkg : (parseInt(currentElement.data().purchasedType) > 4 ? parseInt(currentElement.data().quantity * currentElement.data().ctnSqm) :(currentElement.data().pkgUnit === '1' ? parseInt(currentElement.data().quantity * (currentElement.data().qtyPkg/currentElement.data().ctnSqm)) : parseFloat(currentElement.data().quantity / currentElement.data().ctnSqm))));
			return currentElement.data().move === true ? parseFloat(accumulator) + parseFloat(quantity) : parseFloat(accumulator) - parseFloat(quantity);
		},initialStock)
		let sqMeters = (productType ===  '1' && pkgUnit ===  '1') ? stockTotal*(ctnSqm/qtyPkg): (productType ===  '1' && pkgUnit ===  '0') ? stockTotal*ctnSqm : 0;
		let meters = (productType ===  '2' && pkgUnit ===  '1') ? stockTotal*ctnSqm*qtyPkg: (productType ===  '2' && pkgUnit ===  '0') ? stockTotal*ctnSqm : 0;
		let kilos = (productType ===  '3' && pkgUnit ===  '1') ? stockTotal*ctnSqm*qtyPkg: (productType ===  '3' && pkgUnit ===  '0') ? stockTotal*ctnSqm : 0;
		let cartons = pkgUnit ===  '1' ? parseInt(stockTotal/qtyPkg) : 0;
		let remaining = stockTotal - (cartons * qtyPkg);
		let others = parseInt(productType) > 3 ? parseInt(remaining / ctnSqm) + ' ' + shortTypes[productType] + ' ': '';
		others = (parseInt(productType) > 3 && pkgUnit ===  '1') ? 'and ' + others: others;
		let pieces = parseInt(productType) > 3 ? parseInt(remaining % ctnSqm) : parseInt(remaining);
		openingStock = pkgUnit ===  '1' ? `${cartons} Cts ${others}and ${pieces} ${shortTypes[0]}` : `${others}${pieces} ${shortTypes[0]}`;
		let meter = productType === '2' ? ' and ' + parseInt(meters % ctnSqm) + ' ' + shortTypes[productType] : ''; 
		let sqMeter = (productType === '1' && pkgUnit ===  '0') ? ' and ' + parseInt(sqMeters % ctnSqm) + ' ' + shortTypes[productType] : ''; 
		let kg = productType === '3' ? ' and ' + parseInt(kilos % ctnSqm) +' '+ shortTypes[productType] : ''; 
		openingStock +=  meter + sqMeter + kg;
	  }
	  
	  const setTableData = (subscriber) => {
		let {pkgUnit,qtyPkg,purchasedType,quantity,ctnSqm,productType,move,sales,saleId,damaged,date,moveId} = subscriber.data();
		let qty = purchasedType === '0' ? quantity : (purchasedType === '1' ? quantity * qtyPkg : (parseInt(purchasedType) > 4 ? parseInt(quantity * ctnSqm) :(pkgUnit === '1' ? parseInt(quantity * (qtyPkg/ctnSqm)) : parseFloat(quantity / ctnSqm))));
		stockTotal = move === true ? parseFloat(stockTotal + qty) : parseFloat(stockTotal - qty);
		let sqMeters = (productType ===  '1' && pkgUnit ===  '1') ? stockTotal*(ctnSqm/qtyPkg): (productType ===  '1' && pkgUnit ===  '0') ? stockTotal*ctnSqm : 0;
		let meters = (productType ===  '2' && pkgUnit ===  '1') ? stockTotal*ctnSqm*qtyPkg: (productType ===  '2' && pkgUnit ===  '0') ? stockTotal*ctnSqm : 0;
		let kilos = (productType ===  '3' && pkgUnit ===  '1') ? stockTotal*ctnSqm*qtyPkg: (productType ===  '3' && pkgUnit ===  '0') ? stockTotal*ctnSqm : 0;
		let cartons = pkgUnit ===  '1' ? parseInt(stockTotal/qtyPkg) : 0;
		let remaining = stockTotal - (cartons * qtyPkg);
		let others = parseInt(productType) > 3 ? parseInt(remaining / ctnSqm) + ' ' + shortTypes[productType] + ' ': '';
		others = (parseInt(productType) > 3 && pkgUnit ===  '1') ? 'and ' + others: others;
		let pieces = parseInt(productType) > 3 ? parseInt(remaining % ctnSqm) : parseInt(remaining);
		let result = pkgUnit ===  '1' ? `${cartons} Cts ${others}and ${pieces} ${shortTypes[0]}` : `${others}${pieces} ${shortTypes[0]}`;
		let meter = productType === '2' ? ' and ' + parseInt(meters % ctnSqm) + ' ' + shortTypes[productType] : ''; 
		let sqMeter = (productType === '1' && pkgUnit ===  '0') ? ' and ' + parseInt(sqMeters % ctnSqm) + ' ' + shortTypes[productType] : ''; 
		let kg = productType === '3' ? ' and ' + parseInt(kilos % ctnSqm) +' '+ shortTypes[productType] : ''; 
		result +=  meter + sqMeter + kg;

		let subQuantity = purchasedType === '0' ? quantity : (purchasedType === '1' ? quantity * qtyPkg : (parseInt(purchasedType) > 4 ? parseInt(quantity * ctnSqm) :(pkgUnit === '1' ? parseInt(quantity * (qtyPkg/ctnSqm)) : parseFloat(quantity / ctnSqm))));

		let sqMeters2 = (productType ===  '1' && pkgUnit ===  '1') ? subQuantity*(ctnSqm/qtyPkg): (productType ===  '1' && pkgUnit ===  '0') ? subQuantity*ctnSqm : 0;
		let meters2 = (productType ===  '2' && pkgUnit ===  '1') ? subQuantity*ctnSqm*qtyPkg: (productType ===  '2' && pkgUnit ===  '0') ? subQuantity*ctnSqm : 0;
		let kilos2 = (productType ===  '3' && pkgUnit ===  '1') ? subQuantity*ctnSqm*qtyPkg: (productType ===  '3' && pkgUnit ===  '0') ? subQuantity*ctnSqm : 0;
		let cts = pkgUnit ===  '1' ? parseInt(subQuantity/qtyPkg) : 0;
		let remaining2 = subQuantity - (cts * qtyPkg);
		let others2 = parseInt(productType) > 3 ? parseInt(remaining2 / ctnSqm) + ' ' + shortTypes[productType] + ' ': '';
		let pcs = parseInt(productType) > 3 ? parseInt(remaining2 % ctnSqm) : parseInt(remaining2);
		let meter2 = productType === '2' ? ' and ' + parseInt(meters2 % ctnSqm) + ' ' + shortTypes[productType] : ''; 
		let sqMeter2 = (productType === '1' && pkgUnit ===  '0') ? ' and ' + parseInt(sqMeters2 % ctnSqm) + ' ' + shortTypes[productType] : ''; 
		let kg2 = productType === '3' ? ' and ' + parseInt(kilos2 % ctnSqm) +' '+ shortTypes[productType] : ''; 
		let result2 = pkgUnit ===  '1' ? `${cts} Cts ${others2}and ${pcs} ${shortTypes[0]}` : `${others2}${pcs} ${shortTypes[0]}`;
		others2 = (parseInt(productType) > 3 && pkgUnit ===  '1') ? 'and ' + others2: others2;
		result2 +=  meter2 + sqMeter2 + kg2;
		let newRow = {
			dates: sales === true ? saleId : (damaged === true ? date : moveId),
			product:subscriber.data().product,
			debit:(sales === true || damaged === true) ? result2 + (damaged === true ? ' (damaged)': '') : '',
			credit:move === true ? result2 : '',
			balance:result,
		}
		reportData.push(newRow);
	  };

	  const handleCancel = () => {
		setFdate(0)
		setProduct('')
		setStore('')
		setVisible(false)
		setLoaded(false);
		form.setFieldsValue({shop:''})
	};

	const handleProductChange = (value) => {
		setProduct(value);
	};

	const handleShopChange = (value) => {
		setStore(value);
	};

	const disabledDate = (current) => {
		return current && current > moment().endOf("day");
	} 

	  const handleOk = async () => {
		try {
			setLoading(true);
			const fieldsValue = await form.validateFields();
			let rangeValue = fieldsValue['range-picker'];
			let fdate = new Date(rangeValue[0].format('YYYY-MM-DD')).setHours(0,0,0,0)
			let tdate = new Date(rangeValue[1].format('YYYY-MM-DD')).setHours(23,59,59,999)
			setFdate(fdate);
			reportData = [{dates:fdate, product:'opening stock',debit:'',credit:'',balance:openingStock}];
			setStore(fieldsValue.shop)
			records.filter(x => x.data().date >= fdate &&  x.data().date < tdate)
			.map(subscriber => setTableData(subscriber));
			setLoaded(true);
			setVisible(false);
		} catch (errInfo) {}
	  };

	if(!multiShop && !store && singleShop){
	setStore(singleShop);
	form.setFieldsValue({'shop': singleShop});
	}
	return (
		<Card>
			<Flex alignItems="center" justifyContent="between" mobileFlex={false}>
				<Flex className="mb-1" mobileFlex={false}>
					<div className="mr-md-3">
						<Title level={4}>Generate Report</Title>
					</div>
				</Flex>
				<div>
					<Button onClick={() => setVisible(true)} size="small" type="primary" icon={<CloudDownloadOutlined />} block>Generate</Button>
				</div>
			</Flex>
			{loaded && (<List customers={reportData} loading={recordsLoading} setVisible={setVisible}/>)}
			<Modal
              title="New Report"
              visible={visible}
              onOk={handleOk}
              onCancel={handleCancel}
            >
				<Form layout="vertical" form={form}>
					<Row gutter={16}>
						<Col xs={24} sm={24} md={24}>
							<Form.Item name="range-picker" label="Pick Dates" {...rangeConfig}>
								<RangePicker className="w-100" disabledDate={disabledDate}/>
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={24} className={multiShop ? '' : 'd-none'}>
							<Form.Item
								name="shop"
								label="Store"
								rules={[{ required: true, message: 'Select a store ' }]}
							>
								<Select placeholder=" --Select-- " onChange={handleShopChange}>
								{!storesLoading && stores.docs.map((store, index) => (
									<Option key={index} value={store.data()._id}>
										{store.data().name}
									</Option>
								))}
								</Select>
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={24}>
							<Form.Item
								label="Product"
								name="product"
								rules={[{ required: true, message: `Please select product` }]}
							>
								<Select
									className="w-100"
									showSearch
									optionFilterProp="children"
									filterOption={(input, option) =>
									option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
									}
									onChange={handleProductChange}
									>
										{allItems.map(({ label, index, value}) => (
										<Option key={index} value={value}>
											{label}
											</Option>
										))}
								</Select>
							</Form.Item>
						</Col>
					</Row>
				</Form>
            </Modal>
		</Card>
	)
}

export default Report
